<template>
  <!--页面渲染的容器-->
  <div :id="`router_page_${routerId}`" :style="{zIndex:index}" :data-pageid="pageid" class="idm_render_engine_page_container">
    <!--弹出框容器-->
    <div
      :id="`idm_dialog_wrap_${routerId}`"
      v-show="dialogVisibleStatus"
      class="idm_dialog_wrap_preview"
      :hideing="`${dialogIsHideing ? 'hideing' : ''}`"
    >
      <div
        class="idm_dialog_shade_layer"
        :class="'idm_dialog_shade_layer_' + item.packageid"
        v-for="(item, index) in dialogObjectList"
        :key="index"
        :dialog-id="item.packageid"
        @click="layerClickHandle"
      ></div>
    </div>
    <div
      class="idm_page_root"
      :id="'PAGE_' + PageSettingData.id+'_'+routerId"
      :idm-com-anchor-id="PageSettingData.id"
      ref="idm_page_root"
      :class="`${PageSettingData.id}`"
    ></div>
    <transition name="idm_page_user_defined_topbar">
      <div v-if="pageUserDefinedStatus" class="idm_page_user_defined_topbar">
        <div  class="idm_page_user_defined_logo">
          <svg t="1659591467192" class="user-defined-logo-svg" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="1396" width="32" height="32"><path d="M727.008 487.232l194.016-184.32a99.2 99.2 0 0 0 0-140.288l-48.416-48.416a99.2 99.2 0 0 0-138.464-1.76L544.64 292.384l-184.064-196.64-1.504-1.568a64.832 64.832 0 0 0-91.712-0.384L129.184 231.968a64.8 64.8 0 0 0-1.12 90.144l181.344 193.728-171.456 162.88a99.264 99.264 0 0 0-28.256 49.28l-28.992 123.744a65.632 65.632 0 0 0 82.4 77.92l119.296-35.136a99.744 99.744 0 0 0 40.32-23.232l169.056-160.608 203.616 217.536 1.504 1.568a64.832 64.832 0 0 0 91.712 0.384l138.176-138.176a64.8 64.8 0 0 0 1.12-90.144l-200.896-214.624zM319.424 786.176l-90.112-90.112a31.488 31.488 0 0 0-9.792-6.496L667.104 264.352l94.272 94.272c1.408 1.408 3.168 2.08 4.768 3.168L319.424 786.176zM778.208 158.784a35.2 35.2 0 0 1 49.12 0.64l48.416 48.416c13.76 13.76 13.76 36.032-0.64 50.4l-64.448 61.216c-1.28-2.08-2.24-4.288-4.064-6.112l-93.12-93.12 64.736-61.44zM288.512 399.904c8-0.128 16-3.168 22.112-9.28l48-48a31.968 31.968 0 1 0-45.248-45.248l-48 48a31.68 31.68 0 0 0-8.928 20.256L174.816 278.4c-0.512-0.512-0.512-1.024-0.352-1.152L312.64 139.04c0.128-0.128 0.672-0.128 1.248 0.416l184.384 196.992-142.432 135.328-67.328-71.872zM145.024 868.288a1.6 1.6 0 0 1-2.016-1.92l28.992-123.744c0.992-4.16 2.944-7.968 5.312-11.488a31.808 31.808 0 0 0 6.752 10.144l88.288 88.288a35.072 35.072 0 0 1-8 3.552l-119.328 35.168z m598.336 16.672c-0.128 0.128-0.672 0.128-1.248-0.416l-125.6-134.176a31.232 31.232 0 0 0 14.08-7.712l48-48a31.968 31.968 0 1 0-45.248-45.248l-48 48a31.68 31.68 0 0 0-7.296 11.904l-39.904-42.656 142.432-135.328 200.576 214.304c0.48 0.512 0.48 1.024 0.352 1.152l-138.144 138.176z" p-id="1397"></path></svg>
          <span style="margin-left:10px" >自定义工作台</span>
          <svg v-if="!pagePropData.switchUserCustomizationCommutant" :style="{'transform':`rotate(${openUserCustomizationLeftBar?180:0}deg)`}" @click="openUserCustomizationLeftBar=!openUserCustomizationLeftBar" t="1659592354438" class="fold-svg" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="17129" width="24" height="24"><path d="M130.901333 529.493333l149.205334 130.517334a8.533333 8.533333 0 0 0 14.122666-6.4V392.533333a8.533333 8.533333 0 0 0-14.122666-6.4L130.901333 516.608a8.533333 8.533333 0 0 0 0 12.8z" p-id="17130"></path><path d="M128 213.333333m42.666667 0l682.666666 0q42.666667 0 42.666667 42.666667l0 0q0 42.666667-42.666667 42.666667l-682.666666 0q-42.666667 0-42.666667-42.666667l0 0q0-42.666667 42.666667-42.666667Z" p-id="17131"></path><path d="M128 725.333333m42.666667 0l682.666666 0q42.666667 0 42.666667 42.666667l0 0q0 42.666667-42.666667 42.666667l-682.666666 0q-42.666667 0-42.666667-42.666667l0 0q0-42.666667 42.666667-42.666667Z" p-id="17132"></path><path d="M384 469.333333m42.666667 0l426.666666 0q42.666667 0 42.666667 42.666667l0 0q0 42.666667-42.666667 42.666667l-426.666666 0q-42.666667 0-42.666667-42.666667l0 0q0-42.666667 42.666667-42.666667Z" p-id="17133"></path></svg>
        </div>
        <div class="idm_page_user_defined_tip">
          {{pagePropData.switchUserCustomizationCommutant?"当前模式只支持组件位置对调，您可以移动组件进行组件换位等操作":"您可以对可调整的卡片组件进行移动、添加、删除等操作"}}
        </div>
        <div>
          <idm-a-button @click="userDefinedSave" :loading="userDefinedSaveLoading" type="primary">保 存</idm-a-button>
          <idm-a-button @click="userDefinedQuit" style="margin:auto 10px;">退出编辑</idm-a-button>
        </div>
      </div>
    </transition>
    <transition name="idm_page_user_defined_leftbar">
      <div v-if="pageUserDefinedStatus&&openUserCustomizationLeftBar" class="idm_page_user_defined_leftbar">

        <div class="idm_page_user_defined_allmodule_item" :class="item.hidden!==true?'hover-show-remove':''" v-for="(item,index) in originalUserCustomizationModuleObject.children" :key="index">
          <span :title="item.props&&item.props.compositeAttr&&item.props.compositeAttr.title || item.asName">
          {{item.props&&item.props.compositeAttr&&item.props.compositeAttr.title || item.asName}}
          </span>
          <div class="idm_page_user_defined_allmodule_item_op" :title="item.hidden===true?'添加组件':'移除组件'">
            <svg t="1659636928260" class="user-defined-selected-svg" v-if="item.hidden!==true" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="9243" width="18" height="18"><path d="M944.78369497 156.71660696l38.8044445 32.66674931a25.36238224 25.36238224 0 0 1 3.04348577 35.76095873L486.63761776 818.67478965a76.08714794 76.08714794 0 0 1-107.20679132 9.18118218l-2.40942618-2.10507822-2.28261403-2.20652672L58.64741395 508.44212703a25.36238224 25.36238224 0 0 1-0.07608729-35.86240846l35.81168359-35.93849576a25.36238224 25.36238224 0 0 1 35.88777088-0.05072486l296.56233848 295.6239297 482.21497784-572.42897249a25.36238224 25.36238224 0 0 1 35.73559752-3.0688482z" p-id="9244"></path></svg>
            <svg t="1659636887235" class="user-defined-add-svg" @click="addUserDefinedSelect(item)" v-if="item.hidden===true" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="9088" width="18" height="18"><path d="M462.80754569 45.96622222m10.35630616 0l77.6722963 0q10.35630617 0 10.35630616 10.35630617l0 911.35494322q0 10.35630617-10.35630616 10.35630617l-77.6722963 0q-10.35630617 0-10.35630616-10.35630617l0-911.35494322q0-10.35630617 10.35630616-10.35630617Z" p-id="9089"></path><path d="M66.67883457 462.80754569m10.35630618 0l869.9297185 0q10.35630617 0 10.35630618 10.35630616l0 77.6722963q0 10.35630617-10.35630618 10.35630616l-869.9297185 0q-10.35630617 0-10.35630618-10.35630616l0-77.6722963q0-10.35630617 10.35630618-10.35630616Z" p-id="9090"></path></svg>
            <svg t="1659629683475" class="user-defined-remove-svg" @click="removeUserDefinedSelect(item)" v-if="item.hidden!==true" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="9221" width="18" height="18"><path d="M576 512l277.333333 277.333333-64 64-277.333333-277.333333L234.666667 853.333333 170.666667 789.333333l277.333333-277.333333L170.666667 234.666667 234.666667 170.666667l277.333333 277.333333L789.333333 170.666667 853.333333 234.666667 576 512z" p-id="9222"></path></svg>
          </div>
        </div>
      </div>
    </transition>
    <idm-a-modal
      title="组件替换"
      :visible="pageUserDefinedModalVisible"
      @cancel="pageUserDefinedModalVisible=false"
      :footer="null"
      width="666px"
    >
      <div class="idm_page_user_defined_modal_alert">
        当前位置已显示 <span>{{currentContainerShowModuleName}}</span> 组件，您可以选择其他组件交换当前组件显示的位置
      </div>
      <div v-if="customModuleObject" class="idm_page_user_defined_modal_modulelist">
        <div class="idm_page_user_defined_modal_moduleitem" :class="{'active':item.packageid==currentContainerShowPackageid}" v-for="(item,index) in customModuleObject.children||[]" :key="index">
          <div :title="item.props&&item.props.compositeAttr&&item.props.compositeAttr.title || item.asName">
            <label>{{item.props&&item.props.compositeAttr&&item.props.compositeAttr.title || item.asName}}</label>
            <span v-if="item.containerIndex<999999999">已显示在序号 {{item.containerIndex}} 容器中 </span>
          </div>
          <div>
            <div v-if="item.packageid==currentContainerShowPackageid" class="user-defined-modal-selected">
              <svg t="1659636928260" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="9243" width="18" height="18"><path d="M944.78369497 156.71660696l38.8044445 32.66674931a25.36238224 25.36238224 0 0 1 3.04348577 35.76095873L486.63761776 818.67478965a76.08714794 76.08714794 0 0 1-107.20679132 9.18118218l-2.40942618-2.10507822-2.28261403-2.20652672L58.64741395 508.44212703a25.36238224 25.36238224 0 0 1-0.07608729-35.86240846l35.81168359-35.93849576a25.36238224 25.36238224 0 0 1 35.88777088-0.05072486l296.56233848 295.6239297 482.21497784-572.42897249a25.36238224 25.36238224 0 0 1 35.73559752-3.0688482z" p-id="9244"></path></svg>
              已选
            </div>
            <div @click="userDefinedModuleReplace(item,'changeseats')" v-else-if="item.containerIndex<999999999" class="user-defined-modal-changeseats">
              <svg t="1660186463623" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="7012" width="18" height="18"><path d="M629.419 104.311c113.459 11.333 204.663 79.736 224.05 196.882h-65.672l106.087 157.832L999.97 301.193h-73.182c-19.524-157.833-138.99-260.37-289.86-274.979-20.89-1.638-40.277 16.248-40.277 39.049 1.365 19.524 14.745 37.41 32.768 39.048z m-164.25-79.735H96.256c-20.89 0-37.274 17.886-37.274 40.687V416.7c0 22.8 16.384 40.687 37.274 40.687h368.913c20.89 0 37.274-17.886 37.274-40.687V65.263c0-22.801-16.384-40.687-37.274-40.687zM429.261 379.29H132.164V102.673h297.233l-0.136 276.617z m-35.908 540.125c-126.976-11.332-224.052-95.982-228.557-235.93h70.178L129.16 525.79 23.074 683.486h70.178c2.867 180.633 129.843 297.78 292.728 314.027 20.89 1.638 40.277-16.248 40.277-39.049 0.41-19.388-13.653-36.045-32.904-39.049z m534.8-353.075H559.242c-21.027 0-37.274 18.023-37.274 40.687v353.075c0 22.665 16.384 40.55 37.274 40.55h368.913c20.89 0 37.273-17.885 37.273-40.55V607.027c0-22.664-17.886-40.687-37.273-40.687z m-35.908 354.714H595.012V646.076h297.233v274.978z m0 0" p-id="7013"></path></svg>
              换位
            </div>
            <div @click="userDefinedModuleReplace(item,'replace')" v-else class="user-defined-modal-replace">
              <svg t="1660186765079" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="7166" width="18" height="18"><path d="M911.14666667 610.98666667H112.85333333c-23.57333333 0-42.66666667 19.09333333-42.66666666 42.66666666 0 8.96 2.77333333 17.28 7.46666666 24.10666667 2.02666667 4.8 5.12 9.28 8.96 13.22666667L274.66666667 878.93333333c8.32 8.32 19.2 12.48 30.18666666 12.48s21.86666667-4.16 30.18666667-12.48c16.64-16.64 16.64-43.62666667 0-60.37333333L212.58666667 696.32h698.45333333c23.57333333 0 42.66666667-19.09333333 42.66666667-42.66666667s-18.98666667-42.66666667-42.56-42.66666666zM112.85333333 413.01333333h798.29333334c23.57333333 0 42.66666667-19.09333333 42.66666666-42.66666666 0-8.96-2.77333333-17.28-7.46666666-24.10666667-1.49333333-3.52-3.62666667-6.93333333-6.18666667-10.13333333L786.34666667 148.16c-14.93333333-18.24-41.81333333-20.90666667-60.05333334-5.97333333-18.24 14.93333333-20.90666667 41.81333333-5.97333333 60.05333333l102.72 125.44H112.85333333c-23.57333333 0-42.66666667 19.09333333-42.66666666 42.66666667s19.09333333 42.66666667 42.66666666 42.66666666z" p-id="7167"></path></svg>
              替换
            </div>
          </div>
        </div>
      </div>
    </idm-a-modal>
  </div>
</template>

<script>
import mixin from './mixin';
export default {
  mixins:[mixin],
  name: "Page",
  components: {},
  data() {
    return {
      //当前页面的数据
      PageSettingData: {},
      dialogVisibleStatus: false,
      //是否正在隐藏中
      dialogIsHideing: false,
      //窗口的对象
      dialogObjectList: [],
      //控制中心窗口是否显示
      drawerVisible: false,
      //页面属性
      pagePropData: {},
      //页面是否已经在用户自定义状态
      pageUserDefinedStatus:false,
      //保存动画
      userDefinedSaveLoading:false,
      //是否打开组件列表
      openUserCustomizationLeftBar:false,
      //用户定制化的对象
      originalUserCustomizationModuleObject:{children:[]},
      //用户定制化组件选择对象
      pageUserDefinedModalVisible:false,
      //当前容器显示的packageid
      currentContainerShowPackageid:"",
      //当前容器显示的组件名称
      currentContainerShowModuleName:"",
      //当前容器显示的组件对象
      currentContainerShowModuleObject:{
      },
      customModuleObject:null
    };
  },
  props: {
    //页面ID
    pageid: {
      type: String,
    },
    //路由ID，为空代表是主页面
    routerId: {
      type: String,
    },
    //是否预览页面
    devpreview: {
      type: Boolean,
    },
    //是否主页面
    mainPage: {
      type: Boolean,
    },
    index:{
      type:Number
    }
  },
  created() {
    let that = this;
    if (this.devpreview) {
      //取缓存数据
      this.PageSettingData = JSON.parse(window.localStorage["devpreview"]);
      console.log("当前预览的缓存数据", this.PageSettingData);
      IDM.router.pageDataCachePool.put(that.pageid,_.cloneDeep(this.PageSettingData));
      this.loadPageElement();
    } else {
      if(IDM.router.pageDataCachePool.get(this.pageid)){
        this.PageSettingData = _.cloneDeep(IDM.router.pageDataCachePool.get(this.pageid));
        console.log("从缓存池中获取到页面配置数据（同pageid情况下）", this.PageSettingData);
        this.loadPageElement();
      }else{
        this.$previewApi
          .fetchPageSettingDataForVersion({
            pageid: this.pageid,
            version: "", //当前的版本，没有则必须要返回最新的，有版本要返回对应的版本结果
          })
          .then((res) => {
            //获取页面配置数据返回数据结果
            this.PageSettingData = res.data.data;
            IDM.router.pageDataCachePool.put(that.pageid,_.cloneDeep(res.data.data));
            console.log("从数据接口中获取页面配置数据返回数据结果", this.PageSettingData);
            this.loadPageElement();
          }).catch(function (error) {
              IDM.message.error("页面配置信息加载失败！message："+error.message);
          });
      }
    }
  },
  methods: {
    async loadPageElement() {
      let react = false;
      //查找props覆盖树结构中的
      for (
        let index = 0;
        index < this.PageSettingData.page.componentsMap.length;
        index++
      ) {
        const item = this.PageSettingData.page.componentsMap[index];
        if (item.packageid == this.PageSettingData.page.layout.packageid) {
          this.PageSettingData.page.layout.props = item.props;
        }
        if (item.comLangue && item.comLangue.toLocaleLowerCase() == "react") {
          react = true;
        }
      }
      //加载react或者其他技术栈的lib
      await this.initReactOrOtherLib(react);
      //追加js脚本
      if(this.PageSettingData.page.actions&&this.PageSettingData.page.actions.compiled){
       var pageScriptDom = $("<script type=\"text/javascript\" id=\""+this.pageid+"\"/>");
       $("body").append(pageScriptDom.html("var exports={};"+this.PageSettingData.page.actions.compiled))
      }
      //把map中的数据更新下
      this.PageSettingData.page.componentsMap.forEach((item) => {
        item.env = "production";
        //追加路由ID，页面ID
        item.routerId=this.routerId;
        item.pageid = this.pageid;
      });
      //把页面数据注册到消息桥接器中（改造成支持子页面）
      this.IDM.broadcast.registerModule(this.PageSettingData,this.pageid,this.routerId);
      this.setCurrentPageTitle();
      //先加载应用信息
      await this.initAppAndUserInfo(this.PageSettingData.page.layout.props);

      if (this.mainPage) {
        this.$emit("mainPageSettingHandle", this.PageSettingData);
      }
      //组件开始加载之前执行
      this.beforeLoadingFunction();
      //页面的属性渲染
      this.pageAttrPropHandle(this.PageSettingData.page.layout.props);
      //执行组件加载
      this.$nextTick(function () {
        this.initModuleTreeToPage(null, true, this.PageSettingData.page.layout);
      });
    },
    /**
     * 会话保持功能
     * 此处专门为应用程序登录信息为session准备的（在需要登录信息永久保持的情况下才建议开启此功能）
     * 处理流程：
     * 1、用户密码登录或单点登录的时候同时后端通过jwt（用户信息+密钥）生成登录令牌并存储在前端（url参数、cookie、localStorage）
     * 2、IDM在请求获取用户信息接口后发现session掉了则会通过后端提供通过jwt重新进行登录的接口去登录，一旦发现登录成功则会二次去请求获取用户信息接口
     * 3、如果jwt登录也失效了则会执行原来的跳转登录地址
     */
    async silentReLogin(propData,isreload){
      if(propData&&propData.openSessionKeep&&propData.silentReLoginUrl){
        let relogin = false,cacheKeyName=IDM.setting.sessionKeep.cacheKeyName,cacheKeyValue = "";
        console.log("当前页面cookie信息",document.cookie);
        console.log("获取到的cookie信息："+cacheKeyName,IDM.getCookie(cacheKeyName));
        if(window.localStorage[cacheKeyName]){
          cacheKeyValue = window.localStorage[cacheKeyName]
        }else if(IDM.getCookie(cacheKeyName)){
          cacheKeyValue = IDM.getCookie(cacheKeyName)
        }else if(IDM.url.queryObject()[cacheKeyName]){
          cacheKeyValue = IDM.url.queryObject()[cacheKeyName]
        }
        await IDM.http.post(propData.silentReLoginUrl,{
          [cacheKeyName]:cacheKeyValue
        }).then((res) => {
          let resData = res.data;
          if(resData.code=="200"){
            relogin = true;
          }
        }).catch(function (error) {
        });
        if(relogin){
          await this.fetchUserInfo(propData,false,true,true);
        }
      }
    },
    /**
     * 获取用户信息接口
     * propData:页面属性
     * sessionKeep：是否检查使用会话保持
     */
    async fetchUserInfo(propData,sessionKeep,isreload,resetUserInfo){
      let loginStatus = true,that = this;
      await this.$previewApi
        .fetchUserInfo(propData.userInfoUrl, {
          pageid: this.pageid,
          ...this.commonParam()
        })
        .then((res) => {
          let resData = res.data;
          if(resData.code=="200"&&resData.data){
            resData = resData.data;
          }else if(resData.code!="200"){
            loginStatus = false;
          }
          //只有是主页面才会设置用户信息
          (that.mainPage||resetUserInfo)&&IDM.user.setCurrentUserInfo(resData);
          //发送刷新数据
          (that.mainPage||resetUserInfo)&&loginStatus&&isreload&&IDM.broadcast.send({
            type:"linkageReload",
            rangeModule:"",
            message:new Date().getTime()
          },that.routerId)
        })
        .catch(function (error) {
            loginStatus = false;
        });
      if(sessionKeep&&!loginStatus){
        await this.silentReLogin(propData,isreload)
      }
    },
    /**
     * 同步方式加载应用信息和用户信息
     * 并且需要判断一下是否已经设置了登录判断，如果设置了则需要确定用户ID是否为空，为空则跳转到设置好的登录地址
     */
    async initAppAndUserInfo(propDataObject) {
      let that = this;
      let propData = propDataObject.compositeAttr;
      if (propData.openFetchApplicationInfo && propData.applicationInfoUrl&&this.mainPage) {
        await this.$previewApi
          .fetchApplicationInfo(propData.applicationInfoUrl, {
            pageid: this.pageid,
            ...this.commonParam()
          })
          .then((res) => {
            let resData = res.data;
            if(resData.code=="200"&&resData.data){
              resData = resData.data;
            }
            IDM.app.setAppInfo(resData);
          })
          .catch(function (error) {});
      }
      //只会保留主页面的用户信息，如果子页面开启了只有会话丢失了且再次使用重登录功能才会去把当前用户信息重新设置
      if (propData.openFetchUserInfo && propData.userInfoUrl) {
        await this.fetchUserInfo(propData,true);
      }
      let switchUserInfoFiledValue = "";
      if(propData.switchUserInfo&&propData.switchUserInfoFiledName){
        let filedExp = propData.switchUserInfoFiledName,echoDataName="userObject";
        var dataObject = {IDM:window.IDM};
        dataObject[echoDataName] = IDM.user.getCurrentUserInfo();

        if(filedExp){
          filedExp = echoDataName+(filedExp.startsWiths("[")?"":".")+filedExp;
          switchUserInfoFiledValue = window.IDM.express.replace.call(this, "@["+filedExp+"]", dataObject);
        }
      }else if(propData.switchUserInfo){
        switchUserInfoFiledValue = IDM.user.getCurrentUserInfo()&&IDM.user.getCurrentUserInfo().data?IDM.user.getCurrentUserInfo().data.userid:"";
      }
      //判断是否需要登录(这里暂时写死data.userid)
      if (propData.switchUserInfo && !switchUserInfoFiledValue) {
        if (propData.loginPageUrl) {
          //跳转
          window.location.href = IDM.url.getWebPath(propData.loginPageUrl);
        } else {
          window.location.href = "";
        }
      }
      //判断是否开启应用和个性化设置（每个页面都需要的） 
      if(propData.switchUserCustomization&&propData.userCustomizationFetchUrl){
        const customContainerModuleId = propData.userCustomizationModuleContainer&&propData.userCustomizationModuleContainer.length>0?propData.userCustomizationModuleContainer[0].moduleId:"";
      
        that.originalUserCustomizationModuleObject = _.cloneDeep(that.getModuleTree(
          that.PageSettingData.page.layout,
          customContainerModuleId
        ));
        //获取定制化后的结果
        await this.$previewApi
          .fetchUserCustomizationInfo(propData.userCustomizationFetchUrl, {
            pageid: this.pageid,
            version:this.PageSettingData.pageBaseInfo.version,
            urlData:JSON.stringify(this.commonParam())
          })
          .then((res) => {
            if(res&&res.data&&res.data.data&&res.data.data.customData){
              //定制结果不为空，用用户定制的结果去跟页面结构递归循环去比对然后对他们修改排序
              let customData = res.data.data.customData instanceof Object?res.data.data.customData:JSON.parse(res.data.data.customData);
              //获取排序后的数组
              const userCustomModuleArray = customData.children;
              if(!userCustomModuleArray||(userCustomModuleArray&&userCustomModuleArray.length==0)){
                return;
              }
              //比对找到组件（等于customData.packageid）下的所有数组，然后分别对他们进行数组调整
              let reSortModule = that.getModuleTree(
                that.PageSettingData.page.layout,
                customData.packageid
              );
              if(reSortModule&&reSortModule.children){
                //重新排序
                let newChildren = [],allUserCustomModuleIds=[];
                userCustomModuleArray.forEach((item,index)=>{
                  allUserCustomModuleIds.push(item.packageid);
                  //用户没有开启组件互换才去赋值
                  if(!propData.switchUserCustomizationCommutant){
                    let lsmodule = that.getModuleTree(that.originalUserCustomizationModuleObject,item.packageid);
                    lsmodule?lsmodule.hidden = item.hidden:null;
                  }
                  for (let index = 0; index < reSortModule.children.length; index++) {
                    const element = reSortModule.children[index];
                    if(item.packageid == element.packageid&&item.hidden!==true){
                      element.containerIndex = item.containerIndex;
                      element.relationType = item.relationType;
                      element.relationId = item.relationId;
                      newChildren.push(element);
                      break;
                    }
                  }
                })
                //没有在个性化定制里面的组件
                for (let index = 0; index < reSortModule.children.length; index++) {
                  const element = reSortModule.children[index];
                  if(allUserCustomModuleIds.indexOf(element.packageid)==-1){
                    newChildren.push(element)
                  }
                }
                reSortModule.children = newChildren;
              }
            }
          })
          .catch(function (error) {});
      }

      //应用主题：接口的方式    主页面才会请求的
      if(that.mainPage&&propData.openIdmTheme&&propData.themeFetchType=="customInterface"&&propData.themeInterfaceUrl){
        await this.$previewApi
          .fetchThemeInfo(propData.themeInterfaceUrl, {
            pageid: this.pageid,
            ...this.commonParam()
          })
          .then((res) => {
            that.themeInterfaceHandle(res.data,"resultData")
          })
          .catch(function (error) {});
      }else if(that.mainPage&&propData.openIdmTheme&&propData.themeFetchType=="customFunction"&&propData.themeCustomFunction&&propData.themeCustomFunction.length>0){
        //自定义处理函数
        let switchThemeValue = "";
        propData.themeCustomFunction.forEach(sitem=>{
          try {
            switchThemeValue = window[sitem.name]&&window[sitem.name].call(this,{
              ...this.commonParam(),
              customParam:sitem.param,
              _this:that
            });
          } catch (error) {
          }
        })
        IDM.theme.setCurrentThemeInfo(switchThemeValue);
      }
    },
    /**
     * 页面用户自定义状态切换触发事件
     * status：true 进入编辑状态   false  退出编辑状态
     * save: true 且 status的参数为 false 的时候则是需要保存状态,其他情况则是不执行保存状态
     */
    pageUserDefinedStatusChangeHandle(status=true,save = false){
      let that = this;
      this.pageUserDefinedStatus = status;
      let propData = this.PageSettingData.page.layout.props.compositeAttr;
      //首先找出要自定义的容器组件
       /**
       * 组件范围选择返回的格式：
       * [
       *  {
       *  moduleId:packageid,
       *  moduleName:asName
       *  }
       * ]
       */
      const customContainerModuleId = propData.userCustomizationModuleContainer&&propData.userCustomizationModuleContainer.length>0?propData.userCustomizationModuleContainer[0].moduleId:"";
      if(!customContainerModuleId){
        IDM.message.info("没有可以自定义的容器组件！");
        return;
      }
      //对容器组件查找
      let customModuleObject = this.getModuleTree(
        this.PageSettingData.page.layout.children,
        customContainerModuleId
      );
      if(!customModuleObject){
        return false;
      }
      this.customModuleObject = customModuleObject;
      const customModuleRectInfo =  document.querySelector(`#router_page_${that.routerId}`).querySelector(`#${customModuleObject.packageid}`).getBoundingClientRect();
      let eleDiffPos = {},userDefinedDomList,userDefinedContainerDomList,moveDomBox,
        switchUserCustomizationCommutant = propData.switchUserCustomizationCommutant;
      this.openUserCustomizationLeftBar = !switchUserCustomizationCommutant;
      let currentMoveDom,placeholderDom,hoverContainerDom;
      //minYDom：最接近的dom，locationStr：居于上面还是下面
      let minYDom,locationStr="";
      //拖拽的移动
      !this.moduleMoveEvent?this.moduleMoveEvent = function(e){
        e.stopPropagation();
        e.preventDefault();
        const packageid = e.target.dataset.packageid;
        //移动的时候要改变 idm_module_user_defined_movebox 的 left、top
        //用鼠标的坐标减去元素相对于鼠标的差值
        var position = {};
        position.x = e.clientX-eleDiffPos.x;
        position.y = e.clientY-eleDiffPos.y;
        moveDomBox.style.left=position.x+"px";
        moveDomBox.style.top=position.y+"px";
        //计算横向中心点坐标
        const moveDomCenterX = position.x+(moveDomBox.getBoundingClientRect().width/2),
        moveDomCenterY = position.y + (moveDomBox.getBoundingClientRect().height/2),
        moveDomLeftX = position.x,
        moveDomRightX = position.x+moveDomBox.getBoundingClientRect().width,
        moveDomTopY = position.y,
        moveDomBottomY = position.y+moveDomBox.getBoundingClientRect().height;
        let isCustomModuleDomOut = false;
        if(customModuleRectInfo.x>moveDomRightX||customModuleRectInfo.x+customModuleRectInfo.width<moveDomLeftX||
        customModuleRectInfo.y>moveDomBottomY||customModuleRectInfo.y+customModuleRectInfo.height<moveDomTopY
        ){
          isCustomModuleDomOut = true;
        }
        //对可调整的（class=idm_module_user_defined）要进行监听是否触发到元素上了，
        //有两种形式，同一容器下直接排序方式，非同一容器下直接换位或者换位插入（要取页面配置）
        //idm_module_user_defined_placeholderbox 即将要插入的位置 
        //当拖拽的元素坐标最大Y轴大于当前元素的Y轴不超过10px则触发换位或排序
        //crossYDomList:Y轴有交叉的
        //topMinDom:X中心点最近、距离moveTop最小的（Y轴无交叉的）
        //bottomMinDom:X中心点最近、距离moveBottom最小的（Y轴无交叉的）
        //centerMinDomList:包含了X中心点的集合
        let crossYDomList = [],coverXDomList=[],centerCrossXDomList=[];
        userDefinedDomList&&userDefinedDomList.forEach(userDefinedDomItem=>{
          //先得出符合Y轴的item
          const itemRectInfo = userDefinedDomItem.parentElement.getBoundingClientRect();
          // console.log("🚀 ~ file: Page.vue ~ line 413 ~ moveEvent ~ itemRectInfo", itemRectInfo)
          const itemTopY = itemRectInfo.y,
          itemBottomY = itemRectInfo.y+itemRectInfo.height;
          if((itemTopY<=moveDomTopY&&moveDomTopY<=itemBottomY)||(itemBottomY>=moveDomBottomY&&itemTopY<=moveDomBottomY)){
            //代表顶部或底部都在当前组件范围内
            crossYDomList.push(userDefinedDomItem.parentElement);
          }
          //顶部和底部覆盖了小组件的情况(有交叉的情况)
          if(itemTopY>moveDomTopY&&itemBottomY<moveDomTopY&&(
            (itemRectInfo.x<moveDomLeftX&&itemRectInfo.x+itemRectInfo.width>moveDomLeftX)||
            (itemRectInfo.x+itemRectInfo.width<moveDomRightX&&itemRectInfo.x+itemRectInfo.width>moveDomLeftX)||
            (moveDomLeftX<itemRectInfo.x&&itemRectInfo.x+itemRectInfo.width>moveDomLeftX&&itemRectInfo.x<moveDomRightX)
          )){
            coverXDomList.push(userDefinedDomItem.parentElement);
          }
          if(moveDomCenterX>itemRectInfo.x&&moveDomCenterX<(itemRectInfo.x+itemRectInfo.width)){
            centerCrossXDomList.push(userDefinedDomItem.parentElement);
          }
        })
        // console.log("🚀 ~ file: Page.vue ~ line 420 ~ moveEvent ~ userDefinedDomList", userDefinedDomList)
        // console.log("🚀 ~ file: Page.vue ~ line 410 ~ moveEvent ~ crossYDomList", crossYDomList)
        //过滤完中心点X的dom后
        let filtrXcrossYDomList = [];
        if(crossYDomList.length>0){
          //在Y轴有交叉的取出离中心点最近的
          crossYDomList.forEach(userDefinedDomItem=>{
            const itemRectInfo = userDefinedDomItem.getBoundingClientRect();
            if(moveDomCenterX>itemRectInfo.x&&moveDomCenterX<(itemRectInfo.x+itemRectInfo.width)){
              filtrXcrossYDomList.push(userDefinedDomItem);
            }
          })
        }
        //排出取出最小的距离
        let minYNumber=999999999;
        if(filtrXcrossYDomList.length>0){
          //此处只有交叉的情况
          filtrXcrossYDomList.forEach(userDefinedDomItem=>{
            const itemRectInfo = userDefinedDomItem.getBoundingClientRect();
            const itemTopY = itemRectInfo.y,
            itemBottomY = itemRectInfo.y+itemRectInfo.height;
            if(moveDomTopY-itemTopY<minYNumber){
              minYDom = userDefinedDomItem;
              minYNumber = moveDomTopY-itemTopY;
              //当前元素上面
              locationStr="top";
            }
            if(itemBottomY-moveDomBottomY<minYNumber){
              minYDom = userDefinedDomItem;
              minYNumber = itemBottomY-moveDomBottomY;
              //当前元素下面
              locationStr="bottom";
            }
          })
        }else if(coverXDomList.length>0){
          //此处只有Y轴被完全覆盖的
          coverXDomList.forEach(userDefinedDomItem=>{
            const itemRectInfo = userDefinedDomItem.getBoundingClientRect();
            //这里要找出Y轴的中心点离哪个最近
            if(moveDomCenterY>itemRectInfo.y+itemRectInfo.height&&moveDomCenterY-itemRectInfo.y-itemRectInfo.height<minYNumber){
                minYDom = userDefinedDomItem;
                minYNumber = moveDomTopY-itemRectInfo.y-itemRectInfo.height;
                //当前元素下面
                locationStr="bottom";
            }
            if(moveDomCenterY<itemRectInfo.y&&itemRectInfo.y-moveDomCenterY<minYNumber){
                minYDom = userDefinedDomItem;
                minYNumber = itemRectInfo.y-moveDomBottomY;
                //当前元素上面
                locationStr="top";
            }
          });
          if(!minYDom){
            //上述结果如果没有，那就是中心点在里面
            coverXDomList.forEach(userDefinedDomItem=>{
              const itemRectInfo = userDefinedDomItem.getBoundingClientRect();
              if(moveDomCenterY<=itemRectInfo.y+itemRectInfo.height&&moveDomCenterY>=itemRectInfo.y){
                //在中心，取出离X轴中心最近的
                if(Math.abs(moveDomCenterX-(itemRectInfo.x+itemRectInfo.width/2))<minYNumber){
                   minYDom = userDefinedDomItem;
                   minYNumber = Math.abs(moveDomCenterX-(itemRectInfo.x+itemRectInfo.width/2));
                }
                //当前元素下面
                locationStr="bottom";
              }
            });
          }
        }else if(centerCrossXDomList.length>0&&isCustomModuleDomOut){
          //此处只有要移动的组件完全在底部或者顶部的可能性
          centerCrossXDomList.forEach(userDefinedDomItem=>{
            const itemRectInfo = userDefinedDomItem.getBoundingClientRect();
            if(moveDomTopY>itemRectInfo.y+itemRectInfo.height){
              //在底部
              if(moveDomTopY-itemRectInfo.y-itemRectInfo.height<minYNumber){
                minYDom = userDefinedDomItem;
                minYNumber = moveDomTopY-itemRectInfo.y-itemRectInfo.height;
                //当前元素下面
                locationStr="bottom";
              }
            }
            if(moveDomBottomY<itemRectInfo.y){
              //在顶部
              if(itemRectInfo.y-moveDomBottomY<minYNumber){
                minYDom = userDefinedDomItem;
                minYNumber = itemRectInfo.y-moveDomBottomY;
                //当前元素上面
                locationStr="top";
              }
            }
          })
        }
        //容器
        userDefinedContainerDomList.forEach(userDefinedContainerDomItem=>{
            const itemRectInfo = userDefinedContainerDomItem.getBoundingClientRect();
            //中心点在容器内代表是要放到哪个容器中
            if(moveDomCenterX>itemRectInfo.x&&moveDomCenterX<itemRectInfo.x+itemRectInfo.width
            &&
            moveDomCenterY>itemRectInfo.y&&moveDomCenterY<itemRectInfo.y+itemRectInfo.height
            ){
              hoverContainerDom = userDefinedContainerDomItem;
            }
        })
        if(switchUserCustomizationCommutant){
          //代表是换位
          userDefinedContainerDomList.forEach(item=>{
            IDM.removeClasss(item,"idm_module_user_defined_placeholderstyle")
          })
          hoverContainerDom&&IDM.addClasss(hoverContainerDom,"idm_module_user_defined_placeholderstyle")
        }
        //要判断是否  switchUserCustomizationCommutant  = true  换位，如果换位则需要给捕捉到的容器给一个遮罩层即可，就不用位移占位元素了
        if(minYDom&&!switchUserCustomizationCommutant){
          let parentDom = minYDom.parentElement;
          if(locationStr=="top"){
            parentDom.insertBefore(placeholderDom,minYDom);
          }else{
            //bottom
            if (parentDom.lastChild == minYDom){
              parentDom.appendChild(placeholderDom)
            }else{
              parentDom.insertBefore(placeholderDom,minYDom.nextSibling);
            }
          }
        }else if(!switchUserCustomizationCommutant&&hoverContainerDom.childElementCount==0){
          //代表是空的
          hoverContainerDom.appendChild(placeholderDom)
        }
      }:null;
      //松开事件
      !this.moduleMoveUpEvent?this.moduleMoveUpEvent = function(e){
        e.stopPropagation();
        e.preventDefault();
        //要移除这些事件，否则会影响其他功能
        document.removeEventListener("mousemove",that.moduleMoveEvent);
        document.removeEventListener("mouseup",that.moduleMoveUpEvent);
        if(switchUserCustomizationCommutant&&hoverContainerDom){
          let lastElement = placeholderDom,parentDom = placeholderDom.parentElement;
          console.log("🚀 ~ file: Page.vue ~ line 568 ~ moveUpEvent ~ hoverContainerDom", hoverContainerDom.childNodes)
          if(hoverContainerDom.childNodes){
            const childElementCount = _.cloneDeep(hoverContainerDom.childElementCount);
            for (let index = 0; index < childElementCount; index++) {
              const element = hoverContainerDom.childNodes[0];
              //更新结构
              that.changePageSettingModuleSort(element.id,index==0?currentMoveDom.id: lastElement.id,"bottom");
              parentDom.insertBefore(element,lastElement.nextSibling);
              lastElement = element;
            }
          }
          //插入悬浮容器中
          hoverContainerDom.appendChild(currentMoveDom);
          //更新结构
          that.changePageSettingModuleSort(currentMoveDom.id,hoverContainerDom.getAttribute("idm-container-index"),"inner")
        }else{
          //插入到占位前面
          placeholderDom.parentElement.insertBefore(currentMoveDom,placeholderDom);
          //更新结构
          minYDom?that.changePageSettingModuleSort(currentMoveDom.id,minYDom.id,locationStr):(
            hoverContainerDom?that.changePageSettingModuleSort(currentMoveDom.id,hoverContainerDom.getAttribute("idm-container-index"),"inner"):null
          );

        }
        //移除占位
        placeholderDom.remove();
        //移除移动盒子
        moveDomBox.remove();
        //移除悬浮遮罩占位样式
        userDefinedContainerDomList.forEach(item=>{
          IDM.removeClasss(item,"idm_module_user_defined_placeholderstyle")
        })
        minYDom=null;
        locationStr="";
        //发送组件消息
        IDM.broadcast.send({
          type:"resendAutoLayout",
          rangeModule:"",
          message:{}
        })
      }:null;
      //按下事件
      !this.moduleMousedownEvent?this.moduleMousedownEvent = function(e){
        e.stopPropagation();
        e.preventDefault();
        const packageid = e.target.dataset.packageid;
        if(!packageid){
          return;
        }
        that.isChangeUserDefined = true;
        document.addEventListener("mousemove",that.moduleMoveEvent);
        document.addEventListener("mouseup",that.moduleMoveUpEvent);
        //要拷贝组件至绝对定位、注意组件的容器宽高是要跟现在保持一致的
        console.log("🚀 ~ file: Page.vue ~ line 397 ~ mousedownEvent ~ e", e)
        //获取当前packageid组件的实际宽高值
        currentMoveDom = document.querySelector(`#router_page_${that.routerId}`).querySelector(`#${packageid}`)
        if(!currentMoveDom){
          return;
        }
        const rectInfo = currentMoveDom.getBoundingClientRect();
        console.log("🚀 ~ file: Page.vue ~ line 401 ~ mousedownEvent ~ rectInfo", rectInfo);
        //创建一个绝对定位的容器，用于存放剪切出来的组件
        moveDomBox = document.createElement("div");
        moveDomBox.id="idm_module_user_defined_movebox";
        moveDomBox.className="idm_module_user_defined_movebox";
        moveDomBox.style.width = rectInfo.width+"px";
        moveDomBox.style.height = rectInfo.height+"px";

        eleDiffPos.x = e.clientX-rectInfo.x;
        eleDiffPos.y = e.clientY-rectInfo.y;
        //要计算出位置与原来一模一样
        moveDomBox.style.left=e.clientX-eleDiffPos.x+"px";
        moveDomBox.style.top=e.clientY-eleDiffPos.y+"px";
        moveDomBox.style.position = "absolute";
        //创建一个同等宽的占位元素到currentMoveDom位置
        placeholderDom = document.createElement("div");
        placeholderDom.id="idm_module_user_defined_placeholderbox";
        placeholderDom.className="idm_module idm_module_user_defined_placeholderbox";
        // placeholderDom.style.width = "px";
        placeholderDom.style.height = rectInfo.height+"px";
        placeholderDom.dataset.styleHeight = rectInfo.height+"px";
        currentMoveDom.parentElement.insertBefore(placeholderDom,currentMoveDom);
        moveDomBox.appendChild(currentMoveDom);
        document.body.appendChild(moveDomBox);
        
        userDefinedDomList = document.querySelector(`#router_page_${that.routerId}`).querySelectorAll(".idm_module_user_defined");
      }:null;
      //所有容器Dom
      userDefinedContainerDomList = document.querySelector(`#router_page_${that.routerId}`).querySelectorAll(".drag_container[idm-ctrl-id='"+customModuleObject.id+"']");
      that.registerModuleMouseDownEvent();
      if(save&&this.pageUserDefinedStatus===false){
        this.userDefinedSave();
      }
    },
    /**
     * 注册组件事件
     * registerPackageid:要注册的组件ID
     */
    registerModuleMouseDownEvent(registerPackageid){
      let that = this;
      let propData = this.PageSettingData.page.layout.props.compositeAttr;
      this.customModuleObject.children&&this.customModuleObject.children.forEach(module=>{
        if(registerPackageid&&registerPackageid!==module.packageid){
          return;
        }
        //对组件进行添加事件、Dom、移除事件、Dom
        let moduleDom = $(`#router_page_${that.routerId} #${module.packageid}`),
        moduleElement=document.querySelector(`#router_page_${that.routerId}`).querySelector(`#${module.packageid}`);
        if(!moduleElement){
          return;
        }
        if(that.pageUserDefinedStatus){
          const marginLeft = moduleDom.find(">div[idm-ctrl='idm_module']").css('marginLeft'),
          marginRight = moduleDom.find(">div[idm-ctrl='idm_module']").css('marginRight');

          let userDefinedDom = document.createElement("div");
          userDefinedDom.className="idm_module_user_defined";
          userDefinedDom.style.marginLeft = marginLeft;
          userDefinedDom.style.marginRight = marginRight;
          userDefinedDom.dataset.packageid = module.packageid;
          
          if(!propData.switchUserCustomizationCommutant){
            //不是换位就需要能把组件删除
            userDefinedDom.innerHTML = `
            <div title="移除组件">
            <svg t="1659629683475" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="9221" width="16" height="16"><path d="M576 512l277.333333 277.333333-64 64-277.333333-277.333333L234.666667 853.333333 170.666667 789.333333l277.333333-277.333333L170.666667 234.666667 234.666667 170.666667l277.333333 277.333333L789.333333 170.666667 853.333333 234.666667 576 512z" p-id="9222"></path></svg>
            </div>
            `;
            userDefinedDom.querySelector("div").addEventListener("click",function(e){
              e.stopPropagation();
              e.preventDefault();
              //移除组件 hidden=true
              let hiddenPackageid = e.currentTarget.parentElement.dataset.packageid;
              that.customModuleObject.children.forEach(iitem=>{
                if(iitem.packageid==hiddenPackageid){
                  iitem.hidden = true;
                }
              })
              let lsmodule = that.getModuleTree(that.originalUserCustomizationModuleObject,hiddenPackageid);
              lsmodule?lsmodule.hidden = true:null;
              that.$forceUpdate();
              document.querySelector(`#router_page_${that.routerId}`).querySelector(`#${hiddenPackageid}`).remove();
            })
          }else{
            userDefinedDom.innerHTML = `
