            `;
            userDefinedDom.querySelector("div").addEventListener("click",function(e){
              e.stopPropagation();
              e.preventDefault();
              //显示替换窗口
              let currentPackageid = e.currentTarget.parentElement.dataset.packageid;
              that.pageUserDefinedModalVisible = true;
              that.currentContainerShowPackageid = currentPackageid;
              that.currentContainerShowModuleObject = that.getModuleTree(that.PageSettingData.page.layout.children,currentPackageid);
              that.currentContainerShowModuleName = that.currentContainerShowModuleObject.props&&that.currentContainerShowModuleObject.props.compositeAttr&&that.currentContainerShowModuleObject.props.compositeAttr.title || that.currentContainerShowModuleObject.asName
            })
          }
          moduleElement.appendChild(userDefinedDom)
          //给组件注册拖拽事件等等
          moduleElement.addEventListener("mousedown",that.moduleMousedownEvent)
        }else{
          moduleElement&&moduleElement.querySelector(".idm_module_user_defined").remove();
          //取消组件的事件绑定
          moduleElement&&moduleElement.removeEventListener("mousedown",that.moduleMousedownEvent);
        }
      })
    },
    /**
     * moveModuleId：要移动的组件packageid
     * tragetModuleId：要移动到相关位置的组件packageid，如果position为inner则此处的id为容器的编号
     * position：位置：top、bottom、inner
     * 
     */
    changePageSettingModuleSort(moveModuleId,tragetModuleId,position){
      //
      let propData = this.PageSettingData.page.layout.props.compositeAttr;
      //首先找出要自定义的容器组件
       /**
       * 组件范围选择返回的格式：
       * [
       *  {
       *  moduleId:packageid,
       *  moduleName:asName
       *  }
       * ]
       */
      const customContainerModuleId = propData.userCustomizationModuleContainer&&propData.userCustomizationModuleContainer.length>0?propData.userCustomizationModuleContainer[0].moduleId:"";
      let customModuleObject = this.getModuleTree(
        this.PageSettingData.page.layout.children,
        customContainerModuleId
      );
      if(!customModuleObject){
        return false;
      }
      let moveModuleIndex = -1,tragetModuleIndex = -1;
      customModuleObject.children&&customModuleObject.children.forEach((item,index)=>{
        if(item.packageid==moveModuleId&&position=="inner"){
          item.containerIndex = tragetModuleId;
          if(item.relationType&&item.relationType.startsWiths("inner_")){
            item.relationType = "inner_"+tragetModuleId;
          }
        }
        if(item.packageid==moveModuleId){
          moveModuleIndex = index;
        }
        if(item.packageid==tragetModuleId){
          tragetModuleIndex = index;
        }
      })
      if(tragetModuleIndex>-1&&moveModuleIndex>-1&&position!="inner"){
        customModuleObject.children[moveModuleIndex].containerIndex = customModuleObject.children[tragetModuleIndex].containerIndex;
        customModuleObject.children[moveModuleIndex].relationType = position;
        customModuleObject.children[moveModuleIndex].relationId = tragetModuleId;
        
        customModuleObject.children.splice(tragetModuleIndex+(position=="bottom"?1:0),0,customModuleObject.children[moveModuleIndex]);
        customModuleObject.children.splice(moveModuleIndex+(tragetModuleIndex<moveModuleIndex?1:0),1);
      }
    },

    /**
     * 移除用户定制选择的
     */
    removeUserDefinedSelect(item){
      let that = this;
      item.hidden = true;
      let lsmodule = that.getModuleTree(this.PageSettingData.page.layout.children,item.packageid);
      lsmodule?lsmodule.hidden = true:null;
      that.$forceUpdate();
      document.querySelector(`#router_page_${that.routerId}`).querySelector(`#${item.packageid}`).remove();
    },
    /**
     * 添加用户选择的组件
     */
    addUserDefinedSelect(item){
      //添加完成后
      let that = this;
      item.hidden = false;
      let lsmodule = that.getModuleTree(this.PageSettingData.page.layout.children,item.packageid);
      //组件不存在的时候需要追加
      if(!lsmodule){
        let propData = this.PageSettingData.page.layout.props.compositeAttr;
        const customContainerModuleId = propData.userCustomizationModuleContainer&&propData.userCustomizationModuleContainer.length>0?propData.userCustomizationModuleContainer[0].moduleId:"";
        let customContainerModule = that.getModuleTree(this.PageSettingData.page.layout.children,customContainerModuleId);
        customContainerModule.children.push(_.cloneDeep(item));
      }

      lsmodule?lsmodule.hidden = false:null;
      that.$forceUpdate();
      //重新加载组件
      that.moduleReload(item.packageid);
      //加载完毕后还需要重新注册事件
      that.registerModuleMouseDownEvent(item.packageid);
      //重新加载组件需要修改组件的排序
      //找出当前索引的最后一个
      let lastModule;
      this.customModuleObject.children&&this.customModuleObject.children.forEach(module=>{
        if(module.containerIndex==lsmodule.containerIndex&&module.hidden!==true){
          lastModule = module;
        }
      })
      //要重新排序的
      if(lastModule){
        that.changePageSettingModuleSort(item.packageid,lastModule.packageid,"bottom")
      }else{
        that.changePageSettingModuleSort(item.packageid,lsmodule.containerIndex,"inner")
      }
    },
    /**
     * 组件替换功能
     * item:要替换的对象
     * type:替换的类型：replace 和隐藏域的组件替换， changeseats 跟当前组件换位
     */
    userDefinedModuleReplace(item,type){
      let that = this;
      const orgcontainerIndex = that.currentContainerShowModuleObject.containerIndex;
      //把当前容器的组件移出，如果type=replace 则和999999999的容器序号替换，反之和当前容器的进行替换
      that.currentContainerShowModuleObject.containerIndex = item.containerIndex;
      that.currentContainerShowModuleObject.relationType = item.relationType;
      that.currentContainerShowModuleObject.relationId = item.relationId;
      //把当前选中的组件移到当前组件所在的容器位置
      that.changePageSettingModuleSort(item.packageid,orgcontainerIndex,"inner");
      item.relationType = "inner_"+orgcontainerIndex;

      if(type=="changeseats"){
        //需要先移除被换位的，然后再渲染
        document.querySelector(`#router_page_${that.routerId}`).querySelector(`#${item.packageid}`).remove();
      }
      //把要当前替换的全部移除，然后再渲染
      document.querySelector(`#router_page_${that.routerId}`).querySelector(`#${that.currentContainerShowModuleObject.packageid}`).remove();
       //重新加载组件
      that.moduleReload(item.packageid);
      //加载完毕后还需要重新注册事件
      that.registerModuleMouseDownEvent(item.packageid);
      if(type=="changeseats"){
        //重新加载组件
        that.moduleReload(that.currentContainerShowModuleObject.packageid);
        //加载完毕后还需要重新注册事件
        that.registerModuleMouseDownEvent(that.currentContainerShowModuleObject.packageid);
      }
      that.pageUserDefinedModalVisible = false;
      //发送组件消息
      IDM.broadcast.send({
        type:"resendAutoLayout",
        rangeModule:"",
        message:{}
      })
    },
    /**
     * 用户个性化定制保存
     */
    userDefinedSave(){
      let that = this;
      that.userDefinedSaveLoading = true;
      let propData = this.PageSettingData.page.layout.props.compositeAttr;
      const customContainerModuleId = propData.userCustomizationModuleContainer&&propData.userCustomizationModuleContainer.length>0?propData.userCustomizationModuleContainer[0].moduleId:"";
      
      let customModuleObject = this.getModuleTree(
        this.PageSettingData.page.layout.children,
        customContainerModuleId
      );
      if(!customModuleObject){
        that.userDefinedSaveLoading = false;
        return false;
      }

      let saveNewData = _.cloneDeep(customModuleObject);
      this.deleteLayoutAttachAttr(saveNewData)
      //保存到数据库中
      propData.userCustomizationSaveUrl&&IDM.http
        .post(propData.userCustomizationSaveUrl, {
          pageid: this.pageid,
          version:this.PageSettingData.pageBaseInfo.version,
          customData: JSON.stringify(saveNewData),
        })
        .then((res) => {
          res = res?res.data:{};
          that.userDefinedSaveLoading = false;
          if (res.code !== "200") {
              IDM.message.error("个性化定制保存失败！message："+res.message);
              return;
          }
          IDM.message.success("保存成功！");
          //保存成功
          IDM.page.changePageUserDefinedStatus({routerId:that.routerId,status:false});
        })
        .catch((error) => {
            IDM.message.error("个性化定制保存失败！message："+error.message);
            that.userDefinedSaveLoading = false;
        });
    },
    /**
     * 用户个性化定制退出
     */
    userDefinedQuit(){
      //不保存当前的数据，重新刷新当前页面
      if(this.isChangeUserDefined===true){
        window.location.reload();
      }else{
        IDM.page.changePageUserDefinedStatus({routerId:this.routerId,status:false});
      }
    },
    /**
     * 遮罩层直接关闭窗口
     */
    layerClickHandle(e) {
      let dialogId = e.target.getAttribute("dialog-id");
      if (!dialogId) {
        return;
      }
      window.IdmBuiltin_CloseDialog({
        routerId:this.routerId,
        targetModule: [{ moduleId: dialogId }],
      });
    },
    /**
     * 动态检查是否加载react文件或其他文件
     */
    async initReactOrOtherLib(react) {
      //判断是否已经加载过
      if (react&&!IDM.router.getReact()) {
        IDM.router.setReact();
        //动态加载react文件
        await IDM.http
          .importFiles([
            IDM.url.getWebPath("~/lib/react.min.js"),
            IDM.url.getWebPath("~/lib/react-dom.min.js"),
          ])
          .then(() => {
            //加载完成
            console.log("react文件加载成功");
          })
          .catch((err) => {
            console.log("react文件加载出错");
          });
      }
    },
    /**
     * 组件加载之前执行
     */
    beforeLoadingFunction() {
      let propData = this.PageSettingData.page.layout.props.compositeAttr;
      if (
        propData.beforeLoadingFunction &&
        propData.beforeLoadingFunction.length > 0
      ) {
        this.commonCustomEventFunHandle(propData, "beforeLoadingFunction");
      }
    },
    /**
     * 组件加载完后执行
     */
    afterLoadingFunction() {
      let propData = this.PageSettingData.page.layout.props.compositeAttr;
      if (
        propData.afterLoadingFunction &&
        propData.afterLoadingFunction.length > 0
      ) {
        this.commonCustomEventFunHandle(propData, "afterLoadingFunction");
      }
    },
    /**
     * 请求页面设置的所有接口并回调给每个组件
     */
    requestPageCommonInterfaceData(){
      //调用桥接器页面加载(包括实时渲染)完成事件
      this.IDM.broadcast.pageLoadCompleteAfter(this.routerId);

      if(this.isloadPageCommonInterface){
        return;
      }
      this.isloadPageCommonInterface = true;
      console.log("页面首屏组件已全部加载完毕！！！")
      this.afterLoadingFunction();
      var interfaceList = (this.PageSettingData.page.layout.props.compositeAttr?this.PageSettingData.page.layout.props.compositeAttr.interfaceList:[])||[];
      if(interfaceList.length==0){
        return;
      }
      //先获取页面ID，然后再获取所有的组件ID
      var pageId = this.pageid;
      var moduleIdArray = [],componentIdArray=[];
      this.PageSettingData.page.componentsMap.forEach((item) => {
        if(item.packageid!=pageId){
          moduleIdArray.push(item.packageid);
          componentIdArray.push(item.comId);
        }
      });
      //所有地址的url参数转换
      var params = {
        idmPageId:pageId,
        moduleIds:moduleIdArray.join(","),
        componentIds:componentIdArray.join(",")
      };
      let urlObject = IDM.url.queryObject();
      Object.keys(urlObject).forEach((key)=>{
        params[key]=urlObject[key];
      })
      this.orderlyRequestInterface(interfaceList,0,params);
    },
    /**
     * 有序的加载接口并且回调各个组件
     */
    orderlyRequestInterface(interfaceList,index,params){
      if(interfaceList.length<=index){
        return;
      }
      var item = interfaceList[index];
      index = index+1;
      let that = this;
      // preAllowFunction
      if(item.preAllowFunction&&item.preAllowFunction.length>0){
        try {
          let itemParam = {...that.commonParam(),...item.preAllowFunction[0].param};
          let _isPass = window[item.preAllowFunction[0].name]&&window[item.preAllowFunction[0].name].call(this,itemParam);
          if(_isPass===false){
            that.orderlyRequestInterface(interfaceList,index,params);
            return;
          }
        } catch (error) {
        }
      }
      if(item.interfaceMode=="post"&&item.interfaceUrl){
          if(item.formDataFunction&&item.formDataFunction.length>0){
            try {
              let itemParam = {...that.commonParam(),...item.formDataFunction[0].param};
              let formDataParam = window[item.formDataFunction[0].name]&&window[item.formDataFunction[0].name].call(this,itemParam);
              params = {...params,...formDataParam}
            } catch (error) {
            }
          }
          this.$previewApi
          .postPageInterfaceInfo(item.interfaceUrl,params,{
            headers:item.interfaceContentType?{
              "Content-Type": item.interfaceContentType||"application/json;charset=UTF-8"
            }:{}
          })
          .then((res) => {
            //res.data
            //循环调用组件接收数据的方法
            that.callbackModuleSetContext(item,res.data);
            that.orderlyRequestInterface(interfaceList,index,params);
          })
          .catch(function (error) {
            that.orderlyRequestInterface(interfaceList,index,params);
          });
        }else if(item.interfaceMode=="get"&&item.interfaceUrl){
          this.$previewApi
          .getPageInterfaceInfo(item.interfaceUrl,params)
          .then((res) => {
            //res.data
            //循环调用组件接收数据的方法
            that.callbackModuleSetContext(item,res.data);
            that.orderlyRequestInterface(interfaceList,index,params);
          })
          .catch(function (error) {
            that.orderlyRequestInterface(interfaceList,index,params);
          });
        }else{
          that.orderlyRequestInterface(interfaceList,index,params);
        }
    },
    /**
     * 回调组件的设置值的方法
     */
    callbackModuleSetContext(item,resData){
      this.PageSettingData.page.componentsMap.forEach((sitem) => {
                  sitem.idmSetContextValue&&sitem.idmSetContextValue({
                              type:"pageCommonInterface",
                              key:item.resultDataName,
                              data:resData
                          })
            });
      //自定义处理返回结果函数
      if(item.interfaceRunLaterFunction&&item.interfaceRunLaterFunction.length>0){
        var interfaceRunLaterFunction = item.interfaceRunLaterFunction;
        interfaceRunLaterFunction.forEach(sitem=>{
          try {
            window[sitem.name]&&window[sitem.name].call(this,{
              ...this.commonParam(),
              customParam:sitem.param,
              resultData:resData,
              _this:this
            });
          } catch (error) {
          }
        })
      }
      let propData = this.PageSettingData.page.layout.props.compositeAttr||{};
      //应用主题：页面结果集
      if(this.mainPage&&propData.openIdmTheme&&propData.themeFetchType=="pageInterfaceResult"&&propData.themeDataName&&item.resultDataName==propData.themeDataName){
        this.themeInterfaceHandle(resData,propData.themeDataName)
      }
    },
    /**
     * 渲染页面的样式
     */
    pageAttrPropHandle(propDataObject) {
      let propData = propDataObject.compositeAttr;
      this.pagePropData = propData;
      var styleObject = {};
      if (propData.bgSize && propData.bgSize == "custom") {
        styleObject["background-size"] =
          (propData.bgSizeWidth
            ? propData.bgSizeWidth.inputVal + propData.bgSizeWidth.selectVal
            : "auto") +
          " " +
          (propData.bgSizeHeight
            ? propData.bgSizeHeight.inputVal + propData.bgSizeHeight.selectVal
            : "auto");
      } else if (propData.bgSize) {
        styleObject["background-size"] = propData.bgSize;
      }
      if (propData.positionX && propData.positionX.inputVal) {
        styleObject["background-position-x"] =
          propData.positionX.inputVal + propData.positionX.selectVal;
      }
      if (propData.positionY && propData.positionY.inputVal) {
        styleObject["background-position-y"] =
          propData.positionY.inputVal + propData.positionY.selectVal;
      }
      for (const key in propData) {
        if (propData.hasOwnProperty.call(propData, key)) {
          const element = propData[key];
          if (!element && element !== false) {
            continue;
          }
          switch (key) {
            case "defaultIdmTheme":
              IDM.theme.setCurrentThemeInfo(element);
              break;
            case "width":
            case "height":
              styleObject[key] = element;
              break;
            case "bgColor":
              if (element.hex8) {
                styleObject["background-color"] = IDM.hex8ToRgbaString(element.hex8);
              }
              break;
            case "box":
              if (element.marginTopVal) {
                styleObject["margin-top"] = `${element.marginTopVal}`;
              }
              if (element.marginRightVal) {
                styleObject["margin-right"] = `${element.marginRightVal}`;
              }
              if (element.marginBottomVal) {
                styleObject["margin-bottom"] = `${element.marginBottomVal}`;
              }
              if (element.marginLeftVal) {
                styleObject["margin-left"] = `${element.marginLeftVal}`;
              }
              if (element.paddingTopVal) {
                styleObject["padding-top"] = `${element.paddingTopVal}`;
              }
              if (element.paddingRightVal) {
                styleObject["padding-right"] = `${element.paddingRightVal}`;
              }
              if (element.paddingBottomVal) {
                styleObject["padding-bottom"] = `${element.paddingBottomVal}`;
              }
              if (element.paddingLeftVal) {
                styleObject["padding-left"] = `${element.paddingLeftVal}`;
              }
              break;
            case "bgImgUrl":
              styleObject["background-image"] = `url(${IDM.url.getWebPath(
                element
              )})`;
              break;
            case "icoImgUrl":
              try {
                document.head.querySelector("link[rel='icon']").setAttribute("href",IDM.url.getWebPath(element))
              } catch (error) {
              }
              //IDM.url.getWebPath(element)
              var link = document.createElement('link');
              link.type = 'image/x-icon';
              link.rel = 'shortcut icon';
              link.href = IDM.url.getWebPath(element);
              document.getElementsByTagName('head')[0].appendChild(link);
              break;
            case "positionX":
              //背景横向偏移

              break;
            case "positionY":
              //背景纵向偏移

              break;
            case "bgRepeat":
              //平铺模式
              styleObject["background-repeat"] = element;
              break;
            case "bgAttachment":
              //背景模式
              styleObject["background-attachment"] = element;
              break;
            case "border":
              if (element.border.top.width > 0) {
                styleObject["border-top-width"] =
                  element.border.top.width + element.border.top.widthUnit;
                styleObject["border-top-style"] = element.border.top.style;
                if (element.border.top.colors.hex8) {
                  styleObject["border-top-color"] =
                    IDM.hex8ToRgbaString(element.border.top.colors.hex8);
                }
              }
              if (element.border.right.width > 0) {
                styleObject["border-right-width"] =
                  element.border.right.width + element.border.right.widthUnit;
                styleObject["border-right-style"] = element.border.right.style;
                if (element.border.right.colors.hex8) {
                  styleObject["border-right-color"] =
                    IDM.hex8ToRgbaString(element.border.right.colors.hex8);
                }
              }
              if (element.border.bottom.width > 0) {
                styleObject["border-bottom-width"] =
                  element.border.bottom.width + element.border.bottom.widthUnit;
                styleObject["border-bottom-style"] =
                  element.border.bottom.style;
                if (element.border.bottom.colors.hex8) {
                  styleObject["border-bottom-color"] =
                    IDM.hex8ToRgbaString(element.border.bottom.colors.hex8);
                }
              }
              if (element.border.left.width > 0) {
                styleObject["border-left-width"] =
                  element.border.left.width + element.border.left.widthUnit;
                styleObject["border-left-style"] = element.border.left.style;
                if (element.border.left.colors.hex8) {
                  styleObject["border-left-color"] =
                    IDM.hex8ToRgbaString(element.border.left.colors.hex8);
                }
              }

              styleObject["border-top-left-radius"] =
                element.radius.leftTop.radius +
                element.radius.leftTop.radiusUnit;
              styleObject["border-top-right-radius"] =
                element.radius.rightTop.radius +
                element.radius.rightTop.radiusUnit;
              styleObject["border-bottom-left-radius"] =
                element.radius.leftBottom.radius +
                element.radius.leftBottom.radiusUnit;
              styleObject["border-bottom-right-radius"] =
                element.radius.rightBottom.radius +
                element.radius.rightBottom.radiusUnit;
              break;
            case "font":
              styleObject["font-family"] = element.fontFamily;
              if (element.fontColors.hex8) {
                styleObject["color"] = IDM.hex8ToRgbaString(element.fontColors.hex8);
              }
              styleObject["font-weight"] =
                element.fontWeight && element.fontWeight.split(" ")[0];
              styleObject["font-style"] = element.fontStyle;
              styleObject["font-size"] =
                element.fontSize + element.fontSizeUnit;
              styleObject["line-height"] =
                element.fontLineHeight +
                (element.fontLineHeightUnit == "-"
                  ? ""
                  : element.fontLineHeightUnit);
              styleObject["text-align"] = element.fontTextAlign;
              styleObject["text-decoration"] = element.fontDecoration;
              break;
            case "layout":
              if (element.display && element.display == "flex") {
                styleObject["display"] = "flex";
                if (element.direction) {
                  styleObject["flex-direction"] = element.direction;
                }
                if (element.direction) {
                  styleObject["align-items"] = element.align;
                }
                if (element.direction) {
                  styleObject["justify-content"] = element.justify;
                }
              } else if (element.display) {
                styleObject["display"] = element.display;
                styleObject["width"] = "100%";
              }
              break;
            case "pageMinWidth":
              if(element.inputVal&&element.selectVal){
                styleObject["min-width"] = element.inputVal+element.selectVal;
              }
              break;
          }
        }
      }
      this.$nextTick(function (params) {
        if (propData.watermarkType && propData.watermarkType != "none") {
          var _watermarkFont = this.$replace(propData.watermarkFont, {
              IDM: IDM,
            }),
            _watermarkFont1 = this.$replace(propData.watermarkFont1, {
              IDM: IDM,
            });
          //文字水印
          IDM.watermark.set(
            this.$refs.idm_page_root,
            propData.watermarkZindex,
            propData.watermarkType != "font"
              ? propData.watermarkImg
              : _watermarkFont,
            propData.watermarkType == "image" ? "" : _watermarkFont1,
            {
              fontColor:
                propData.watermarkColor && IDM.hex8ToRgbaString(propData.watermarkColor.hex8),
              fontSize: `${propData.watermarkFontSize}px`,
              width: propData.watermarkSize,
              height: propData.watermarkSize,
              rotate: propData.watermarkRotate,
              imgSize: propData.watermarkImgSize,
              imgLeftSize: propData.watermarkImgLeftSize,
              fontLeftSize: propData.watermarkFontLeftSize,
              opacity: propData.watermarkOpacity / 100,
              topSize: propData.watermarkTopSize,
            }
          );
        } else {
          IDM.watermark.close(this.$refs.idm_page_root);
        }
      });
      IDM.setStyleToPageHead("PAGE_" + this.PageSettingData.id+"_"+this.routerId, styleObject);
      //加载外部js和css
      if(propData.resourceList&&propData.resourceList.length>0){
        propData.resourceList.forEach(item=>{
          if(item.resourceType=="js"){
            IDM.module.loadJs(IDM.url.getWebPath(item.resourceUrl),true,function(){

            },{async:item.async,defer:item.defer})
          }else{
            IDM.module.loadCss(IDM.url.getWebPath(item.resourceUrl),true,function(){

            })
          }
        })
      }
      this.convertThemeListAttrToStyleObject(propData);
    },
    
    /**
     * 主题颜色
     */
    convertThemeListAttrToStyleObject(propData) {
      var themeList = propData.themeList;
      if (!themeList) {
        return;
      }
      const themeNamePrefix =
        IDM.setting && IDM.setting.applications && IDM.setting.applications.themeNamePrefix
          ? IDM.setting.applications.themeNamePrefix
          : 'idm-theme-';
      for (var i = 0; i < themeList.length; i++) {
        var item = themeList[i];
        //item.key：为主题样式的key
        //item.mainColor：主要颜色值
        //item.minorColor：次要颜色值
        if(item.key!=IDM.theme.getCurrentThemeInfo()){
            //此处比对是不渲染输出不用的样式，如果页面会刷新就可以把此处放开
            continue;
        }
        let cssObject_color_main = {
          color: item.mainColor ? item.mainColor.hex8+' !important' : ''
        };
        let cssObject_border_main = {
          'border-color': item.mainColor ? item.mainColor.hex8 : ''
        };
        let cssObject_background_main = {
          'background-color': item.mainColor ? item.mainColor.hex8 : ''
        };
        let cssObject_color_minor = {
          color: item.minorColor ? item.minorColor.hex8 : ''
        };
        let cssObject_border_minor = {
          'border-color': item.minorColor ? item.minorColor.hex8 : ''
        };
        let cssObject_background_minor = {
          'background-color': item.minorColor ? item.minorColor.hex8 : ''
        };
        IDM.setStyleToPageHead(
          `.${themeNamePrefix + item.key} .idm_module_user_defined`,
          {
            ...cssObject_border_main
          }
        );
        IDM.setStyleToPageHead(
          `.${themeNamePrefix + item.key} .idm_module_user_defined>div`,
          {
            ...cssObject_background_main
          }
        );
        IDM.setStyleToPageHead(
          `.${themeNamePrefix + item.key} .user-defined-logo-svg,
          .${themeNamePrefix + item.key} .idm_page_user_defined_leftbar svg.user-defined-add-svg,
          .${themeNamePrefix + item.key} .idm_page_user_defined_modal_moduleitem .user-defined-modal-changeseats,
          .${themeNamePrefix + item.key} .idm_page_user_defined_modal_moduleitem .user-defined-modal-replace`,
          {
            ...cssObject_color_main
          }
        );
        IDM.setStyleToPageHead(
          `.${themeNamePrefix + item.key} .idm-ant-btn:active,
          .${themeNamePrefix + item.key} .idm-ant-btn.active,
          .${themeNamePrefix + item.key} .idm-ant-btn-dashed:active,
          .${themeNamePrefix + item.key} .idm-ant-btn-dashed.active`,
          {
            ...cssObject_color_main,
            ...cssObject_border_main
          }
        );
        IDM.setStyleToPageHead(
          `.${themeNamePrefix + item.key} .idm-ant-btn-primary,
          .${themeNamePrefix + item.key} .idm-ant-btn-primary:hover,
          .${themeNamePrefix + item.key} .idm-ant-btn-primary:focus`,
          {
            ...cssObject_background_main,
            ...cssObject_border_main
          }
        );
        IDM.setStyleToPageHead(
          `.${themeNamePrefix + item.key} .idm-ant-btn:hover,
          .${themeNamePrefix + item.key} .idm-ant-btn:focus,
          .${themeNamePrefix + item.key} .idm-ant-btn-dashed:hover,
          .${themeNamePrefix + item.key} .idm-ant-btn-dashed:focus`,
          {
            ...cssObject_color_minor,
            ...cssObject_border_minor
          }
        );
        IDM.setStyleToPageHead(
          `.${themeNamePrefix + item.key} .idm-ant-btn-primary:hover,
          .${themeNamePrefix + item.key} .idm-ant-btn-primary:focus`,
          {
            color:"#FFFFFF",
            ...cssObject_background_minor,
            ...cssObject_border_minor
          }
        );
      }
    },
    /**
     * 渲染加载指定ID的窗口
     * 首先取出对应的数据，判断是否
     */
    renderDialog(moduleId){
      const opModule = this.getModuleTree(
        this.PageSettingData.page.layout.children,
        moduleId
      );
      if(!opModule){
        return false;
      }
      if(opModule.renderType=="firstrender"||opModule.renderType=="activerender"){
        opModule.renderLicence = true;
        if(opModule.renderType=="firstrender"&&opModule.loaded){
          return _.cloneDeep(opModule);
        }else{
          //实时渲染加载之前要把页面组件加载状态设置为false，否则实时渲染的组件消息通信无法准确接收到消息，需要等全部加载完再接收消息
          this.IDM.broadcast.pageModuleLoadCompleteBefore(this.routerId);
          //修改成全部未加载
          this.resetLayoutInnerModuleLoadedStatus(opModule);
          this.initModuleTreeToPage(opModule)
        }
      }
      return _.cloneDeep(opModule);
    },
    setShowDialog(dialogObject){
      this.dialogObjectList.push(dialogObject);
      $("#router_page_"+this.routerId+" #"+dialogObject.packageid).css("display","flex");
      this.dialogVisibleStatus = true;
      this.dialogIsHideing = false;
    },
    /**
     * 关闭当前打开的窗口
     */
    closeThisOpenDialog(ctx){
      var customParam = ctx.customParam?ctx.customParam:ctx;
      var moduleIdArray = [];
      customParam.targetModule.forEach(item=>{
        moduleIdArray.push(item.moduleId);
        this.dialogObjectList.forEach((ditem,dindex)=>{
          if(ditem.packageid==item.moduleId){
            ditem.dialogIsHideing = true;
            $("#router_page_"+this.routerId+" .idm_dialog_shade_layer_"+item.moduleId).attr("hideing","hideing");
            if(ditem.layerOption){
              ditem.layerOption.cancel&&ditem.layerOption.cancel(ditem);
            }
            //如果是确认按钮调用的关闭则去调用yes方法
            if(ctx.isOkEvent){
              ditem.layerOption&&ditem.layerOption.yes&&ditem.layerOption.yes(ditem);
            }
          }
        })
      });
      if(this.dialogObjectList.length<=moduleIdArray.length){
        this.dialogIsHideing = true;
      }
      let that = this;
      that.sendBroadcastMessage({
          type:"linkageCloseDialog",
          message:ctx,
          rangeModule:moduleIdArray
        })
      setTimeout(() => {
        customParam.targetModule.forEach(item=>{
          $("#router_page_"+this.routerId+" #"+item.moduleId).css("display","none");
          that.destroyDialog(item.moduleId);
          //从dialogObjectList中删除
          if(that.dialogObjectList.length==1){
            that.dialogObjectList = [];
            return;
          }
          let existsIndex = -1;
          that.dialogObjectList.forEach((ditem,dindex)=>{
            if(ditem.packageid==item.moduleId){
              existsIndex = dindex;
              if(ditem.layerOption){
                ditem.layerOption.end&&ditem.layerOption.end(ditem);
              }
            }
          })
          if (existsIndex > -1) {
            //存在就清除
            that.dialogObjectList.splice(existsIndex, 1);
          }
        });
        if(that.dialogObjectList.length==0){
          that.dialogVisibleStatus = false;
          that.dialogIsHideing = false;
        }
      }, 500);
      if(ctx.callback){
        ctx.callback();
      }
    },
    /**
     * 销毁指定ID的窗口
     */
    destroyDialog(moduleId){
      const opModule = this.getModuleTree(
        this.PageSettingData.page.layout.children,
        moduleId
      );
      if(opModule&&opModule.renderType=="activerender"){
        $("#router_page_"+this.routerId+" #"+moduleId).remove();
      }
    },
    /**
     * 加载组件树到页面中，递归逐级加载
     * moduleData：要渲染的组件数据树结构
     * ischildren：是否为子组件数据
     * fModule：子组件所在的父级容器组件
     * addNextForModulePackageid：要追加到哪个组件的下面（非子级）的packageid
     */
    initModuleTreeToPage(
      moduleData,
      ischildren,
      fModule,
      addNextForModulePackageid
    ) {
      let that = this;
      moduleData = moduleData || that.PageSettingData.page.layout.children;
      if (!(moduleData instanceof Array)) {
        moduleData = [moduleData];
      }
      //循环取出当前一级未加载的组件
      var loadModule;
      moduleData.forEach((item) => {
        if(item.comType=="dialog"&&item.renderLicence!==true){
          //查找props
          for (let index = 0; index < that.PageSettingData.page.componentsMap.length; index++) {
            const pitem = that.PageSettingData.page.componentsMap[index];
            if (pitem.packageid == item.packageid) {
              item.props = pitem.props;
            }
          }
          let dialogRenderType = item.props.compositeAttr.dialogRenderType;
          //beforehand：不用判断直接放行可以进行渲染  firstrender：按需首次渲染  activerender：激活时渲染
          item.renderType = dialogRenderType;
          //renderLicence：渲染许可，true表示可以进行渲染，false表示暂时不渲染跳过
          item.renderLicence = !(dialogRenderType=="firstrender"||dialogRenderType=="activerender");
          if(!item.renderLicence){
            return;
          }
        }
        if (!item.hasLoading && !loadModule) {
          loadModule = item;
          item.hasLoading = true;
          item.loading = true;
        }
      });
      if (!loadModule) {
        //一级没有待加载的，再去加载子级
        moduleData.forEach((item) => {
          if(item.comType=="dialog"&&!item.renderLicence){
            //渲染许可为false跳过不渲染下面的子级
            return;
          }
          if (item.children && item.children.length > 0) {
            that.initModuleTreeToPage(item.children, true, item);
          }
        });
      } else {
        //追加容器
        var comObject = loadModule;
        comObject.mountComplete = this.moduleMountComplete;
        comObject.moduleReload = this.moduleReload;
        //追加div
        var newComDiv = that.createModuleDom(comObject);
        var containerId = "";
        if(comObject.comType=="dialog"){
          containerId = that.PageSettingData.id;
          $("#idm_dialog_wrap_"+that.routerId).append(newComDiv);
        }else if (addNextForModulePackageid) {
          //添加到下方
          containerId = addNextForModulePackageid;
          $("#router_page_"+that.routerId+" #" + containerId).after(newComDiv);
        } else if (ischildren && fModule) {
          containerId = fModule.packageid;
          if (containerId == that.PageSettingData.id) {
            $("#PAGE_" + containerId+"_"+that.routerId).append(newComDiv);
          } else {
            var containerDom = $("#router_page_"+that.routerId+" #" + containerId);
            containerDom.removeClass("drag_container_empty");
            var containerInnerDom = containerDom.find(">*[idm-ctrl]");
            containerInnerDom.show();
            if (comObject.containerIndex || comObject.containerIndex == "0") {
              //添加内部容器，同时要删除drag_container_empty
              var innercontainer = containerInnerDom.find(
                ".drag_container[idm-ctrl-id='" +
                  fModule.id +
                  "'][idm-container-index=" +
                  comObject.containerIndex +
                  "]"
              );
              innercontainer&&innercontainer.append(newComDiv);
              innercontainer&&innercontainer.removeClass("drag_container_empty");
            } else {
              containerInnerDom.append(newComDiv);
            }
          }
        }
        //关联ID
        comObject.env = "production";
        //追加路由ID，页面ID
        comObject.routerId=this.routerId;
        comObject.pageid = this.pageid;
        //查找props覆盖树结构中的
        for (let index = 0; index < that.PageSettingData.page.componentsMap.length; index++) {
          const item = that.PageSettingData.page.componentsMap[index];
          if (item.packageid == comObject.packageid) {
            comObject.props = item.props;
          }
        }
        that.IDM.module.addModuleToQueue(comObject);
      }

      return loadModule;
    },
    /**
     * 判断是否加载完成,未完成返回false，主要用于拷贝的时候递归加载完成的判断
     * loadType:加载类型：loaded 是否全部加载完成、loading：加载中的、hasLoading：已经开始加载的
     */
    pageModuleDataIsLoaded(moduleData, isnotloadinner,loadType = "hasLoading") {
      let that = this;
      var isLoaded = true;
      moduleData = moduleData || that.PageSettingData.page.layout.children;
      if (!(moduleData instanceof Array)) {
        moduleData = [moduleData];
      }
      moduleData.forEach((item) => {
        if(item.comType=="dialog"&&!item.renderLicence){
          //渲染许可为false跳过不渲染下面的子级
          return;
        }
        if (!item[loadType]) {
          isLoaded = false;
          // if(isnotloadinner&&(item.containerIndex||item.containerIndex=="0")){
          //   isLoaded = true;
          // }
        }
      });
      if (isLoaded) {
        moduleData.forEach((item) => {
          if(item.comType=="dialog"&&!item.renderLicence){
            //渲染许可为false跳过不渲染下面的子级
            return;
          }
          if (item.children && item.children.length > 0 && isLoaded) {
            isLoaded = this.pageModuleDataIsLoaded(
              item.children,
              isnotloadinner,
              loadType
            );
          }
        });
      }
      return isLoaded;
    },
    /**
     * 创建组件dom外部元素
     */
    createModuleDom(comObject) {
      let that = this;
      //包裹最外层的ID
      comObject.packageid = comObject.packageid || "component_" + IDM.UUID();
      //组件内部ID，这里可能不受核心框架控制，需要谨慎使用
      comObject.id = comObject.id || comObject.packageid + "_inner";
      //追加div
      var newComDiv = $("<div />");
      //追加设置属性
      newComDiv.attr("id", comObject.packageid);
      newComDiv.attr("idm-com-anchor-id", comObject.packageid);
      newComDiv.attr("idm-com-id", comObject.comId);
      newComDiv.attr("idm-com-classid", comObject.classId);
      newComDiv.attr("idm-com-classname", comObject.className);
      //追加组件标识
      newComDiv.addClass("idm_module");
      newComDiv.addClass(comObject.packageid);
      //追加内部div，提供给组件加载使用，只有ID，后续将不会再使用
      newComDiv.append($("<div />").attr("id", "idm_"+comObject.id+this.routerId));
      return newComDiv;
    },
    /**
     * 获取指定层级的组件树结构数据
     */
    getModuleTree(module, packageid) {
      if (!(module instanceof Array)) {
        module = [module];
      }
      var _findModule;
      module.forEach((item) => {
        if (item.packageid == packageid) {
          _findModule = item;
        } else if (!_findModule && item.children) {
          _findModule = this.getModuleTree(item.children, packageid);
        }
      });
      return _findModule;
    },
    /**
     * 提供组件或组件内部容器的重新加载
     */
    moduleReload(packageid,innerContainerIndex){
      //先查找模块，然后根据innerContainerIndex来删除对应的加载状态标识
      const reloadModule = this.getModuleTree(
        this.PageSettingData.page.layout.children,
        packageid
      );
      let reloadModuleArray = [reloadModule];
      let parentModule;
      if(innerContainerIndex||innerContainerIndex=="0"){
        //存在内部索引
        reloadModuleArray = [];
        reloadModule&&reloadModule.children&&reloadModule.children.forEach(item=>{
          if(item.containerIndex==innerContainerIndex){
            reloadModuleArray.push(item);
          }
        })
        parentModule = reloadModule;
      }else{
        //找出当前的父级
        let globalIdArray = reloadModule.globalId.split(".");
        const globalId = globalIdArray.length<=2?globalIdArray[0]:globalIdArray[globalIdArray.length-2];
        parentModule = this.getModuleTree(
          this.PageSettingData.page.layout.children,
          globalId
        );
      }
      //删除全部加载状态
      this.resetLayoutInnerModuleLoadedStatus(reloadModuleArray);
      reloadModuleArray&&reloadModuleArray.forEach((item) => {
        $("#router_page_"+this.routerId+" #" + item.packageid).remove();
      });
      
      //调用重新加载
      this.initModuleTreeToPage(
        reloadModuleArray,
        true,
        parentModule,
        false
      );
    },
    /**
     * 组件挂载完成回调事件
     */
    moduleMountComplete(module) {
      let that = this;
      //把事件合并到map中
      this.PageSettingData.page.componentsMap.forEach((item) => {
        if (item.packageid == module.packageid) {
          item.idmBroadcastMessage = module.idmBroadcastMessage;
          item.idmSetContextValue = module.idmSetContextValue;
          item.idmGetContextValue = module.idmGetContextValue;
        }
      });
      console.log("挂载完成，回调返回的数据", module);
      let opModule = this.getModuleTree(
        this.PageSettingData.page.layout.children,
        module.packageid
      );
      //加载完成，不在加载中了
      opModule.loading = false;
      //设置加载完成的状态
      opModule.loaded = true;
      if (opModule.children && opModule.children.length > 0) {
        //有子组件，代表是容器类组件，首先获取是否加载完成且有子元素inner
        const targetNode = $("#" + module.packageid);
        if (targetNode.find(":visible").length == 0) {
          //代表内容元素还是空的，没有加载完成
          var invcount = 0;
          var intval = setInterval(function () {
            invcount++;
            if (
              $("#router_page_"+that.routerId+" #" + module.packageid).find(":visible").length == 0 &&
              invcount <= 20
            ) {
              return;
            }
            clearInterval(intval);
            that.moduleMountCompleteCommon(module);
          }, 5);
        } else {
          //加载时序问题，延迟3毫秒
          setTimeout(() => {
            that.moduleMountCompleteCommon(module);
          }, 3);
        }
      } else {
          //加载时序问题，延迟3毫秒
          setTimeout(() => {
            that.moduleMountCompleteCommon(module);
          }, 3);
      }
      // this.initModuleInnerEventHandle(module);
      // class名称表示：
      // drag_container_empty：空容器类名
      // drag_container：容器，可拖拽过来
      // id作为class类名：样式设置
      // idm_module：所有组件最外层都有的类名
      // drag_container_inlineblock: 代表是行内容器

      // 属性 ：
      // idm-com-anchor-id：代表新生成的唯一组件ID
      // id:代表新生成的唯一组件ID
      // idm-com-id：组件的ID
      // idm-com-classid：组件的包名
      // idm-com-classname：组件的类名
      // idm-container-index：容器索引，在一个组件内有两个或以上的容器就会作用到
    },
    moduleMountCompleteCommon(module){
      let that = this;
      var moduleDom = $("#router_page_"+this.routerId+" #" + module.packageid + "");
      var moduleInnerDom = moduleDom.find(">*[idm-ctrl]");
      var idmCtrl = moduleInnerDom.attr("idm-ctrl"),
        idmCtrlId = moduleInnerDom.attr("idm-ctrl-id"),
        domId = moduleInnerDom.attr("id");
      if (!domId) {
        moduleInnerDom.attr("id", idmCtrlId);
      }
      switch (idmCtrl) {
        case "drag_container": //容器
          moduleDom.addClass("drag_container");
          break;
        case "drag_container_inlineblock": //行内容器
          moduleDom.addClass("drag_container drag_container_inlineblock");
          break;
        case "idm_module": //非容器组件
          break;
      }

      //如果没有加载完成则先判断下，没有完成则继续加载
      if (!this.pageModuleDataIsLoaded(null, true)) {
        this.$nextTick(function () {
          this.initModuleTreeToPage(
            null,
            true,
            this.PageSettingData.page.layout
          );
        });
      }else if (this.pageModuleDataIsLoaded(null, true,"loaded")){
        this.requestPageCommonInterfaceData();
      }
    },
    /**
     * 加载组件内部容器组件事件
     * 参数：
     * module：{packageid:组件包id，组件内部使用的id}
     */
    initModuleInnerEventHandle(module) {
      let that = this;
      //获取是否存在内部容器组件
      var moduleDom = $("#router_page_"+that.routerId+" #" + module.packageid + "");
      var moduleInnerDom = moduleDom.find(
        ".drag_container[idm-ctrl-id='" + module.id + "'][idm-ctrl-inner]"
      );
      if (moduleInnerDom.length > 0) {
        //内部组件重新追加，因为上面哪种加载方式会导致没加载完
        const opModule = this.getModuleTree(
          this.PageSettingData.page.layout.children,
          module.packageid
        );
        this.resetLayoutInnerModuleLoadedStatus(opModule.children);
        if (opModule.children && opModule.children.length > 0) {
          opModule.children.forEach((item) => {
            $("#" + item.packageid).remove();
          });
          this.initModuleTreeToPage(opModule.children, true, opModule);
        }
      }
    },
    /**
     * 加载前重置所有加载状态
     */
    resetLayoutInnerModuleLoadedStatus(module) {
      if (!(module instanceof Array)) {
        module = [module];
      }
      module.forEach((item) => {
        //组件是否已经加载完成
        delete item.loaded;
        //组件是否在加载中...
        delete item.loading;
        //组件是否已经加载
        delete item.hasLoading;
        if (item.children && item.children.length > 0) {
          this.resetLayoutInnerModuleLoadedStatus(item.children);
        }
      });
    },
  },
};
</script>

