export default {
    props: {
    },
    data() {
        return {
        }
    },
    watch: {
    },
    methods: {
      /**
       * 自定义接口与页面接口统一回调处理主题的函数
       */
      themeInterfaceHandle(resultData,echoDataName){
        let that = this;
        let propData = this.PageSettingData.page.layout.props.compositeAttr;
        let themeDataFiled = propData.themeDataFiled,
        themeDataFunction = propData.themeDataFunction,
        switchThemeValue=resultData;
        if(themeDataFiled){
          let filedExp = themeDataFiled;
          var dataObject = {IDM:window.IDM};
          dataObject[echoDataName] = resultData;
  
          if(filedExp){
            filedExp = echoDataName+(filedExp.startsWiths("[")?"":".")+filedExp;
            switchThemeValue = window.IDM.express.replace.call(this, "@["+filedExp+"]", dataObject);
          }
        }
        if(themeDataFunction&&themeDataFunction.length>0){
          var paramObject = {
                ...that.commonParam(),
                _this:that
              };
            paramObject[echoDataName] = resultData;
          themeDataFunction.forEach(sitem=>{
            try {
              switchThemeValue = window[sitem.name]&&window[sitem.name].call(this,{customParam:sitem.param,...paramObject});
            } catch (error) {
            }
          })
        }
        IDM.theme.setCurrentThemeInfo(switchThemeValue);
      },
        /**
         * 设置更新当前页面的标题
         */
        setCurrentPageTitle(){
            //修改页面标题
            document.title = this.PageSettingData.pageBaseInfo.title || IDM.setting.applications.defaultTitle;
        },
        /**
         * 通用自定义函数
         */
        commonCustomEventFunHandle(propData,name){
          let that = this;
          propData = propData||this.pagePropData;
          var customHandle = propData[name],resultData;
          customHandle &&
            customHandle.forEach((item) => {
              resultData = window[item.name] &&
                window[item.name].call(this, {
                  ...that.commonParam(),
                  customParam: item.param,
                  _this: that
                });
            });
          return resultData
        },
        /**
         * 通用的url参数对象
         * 所有地址的url参数转换
         */
        commonParam(){
          let urlObject = IDM.url.queryObject();
          var params = {
            pageId:
              window.IDM.broadcast && window.IDM.broadcast.pageModule
                ? window.IDM.broadcast.pageModule.id
                : "",
            urlData: JSON.stringify(urlObject),
          };
          return params;
        },
        /**
         * 组件通信：发送消息的方法
         * @param {
         *  type:"自己定义的，统一规定的type：linkageResult（组件联动传结果值）、linkageDemand（组件联动传需求值）、linkageReload（联动组件重新加载）
         * 、linkageOpenDialog（打开弹窗）、linkageCloseDialog（关闭弹窗）、linkageShowModule（显示组件）、linkageHideModule（隐藏组件）、linkageResetDefaultValue（重置默认值）"
         *  message:{实际的消息对象},
         *  rangeModule:"为空发送给全部，根据配置的属性中设定的值（值的内容是组件的packageid数组），不取子表的，比如直接 this.$root.propData.compositeAttr["attrKey"]（注意attrKey是属性中定义的bindKey）,这里的格式为：['1','2']"",
         *  className:"指定的组件类型，比如只给待办组件发送，然后再去过滤上面的值"
         *  globalSend:如果为true则全站发送消息，注意全站rangeModule是无效的，只有className才有效，默认为false
         * } object 
         */
        sendBroadcastMessage(object){
            window.IDM.broadcast&&window.IDM.broadcast.send(object);
        },
        /**
         * 保存时候删除的不必要的属性
         */
        deleteLayoutAttachAttr(module) {
          if (!(module instanceof Array)) {
            module = [module];
          }
          module.forEach((item) => {
            delete item.env;
            delete item.hasLoading;
            delete item.loaded;
            delete item.loading;
            delete item.moduleReload;
            delete item.mountComplete;
            delete item.props;
            delete item.routerId;
            if (item.children && item.children.length > 0) {
              this.deleteLayoutAttachAttr(item.children);
            }
          });
        }
    },
    mounted() {
    },
}
